import { use } from './compatibleAlipayJs';
import { MyDingTalkJs } from './compatibleDingtalkJs';

export const IsAlipayBrowser = /alipayclient/.test(navigator.userAgent.toLowerCase());
export const IsDingTalkBrowser = /aliapp\(dingtalk/.test(navigator.userAgent.toLowerCase());
export const IsWindowsPhone = navigator.userAgent.indexOf('Windows Phone') >= 0;
export const IsAndroid = navigator.userAgent.indexOf('Android') > 0 && !IsWindowsPhone;
export const IsIOS = /iP(ad|hone|od)/.test(navigator.userAgent) && !IsWindowsPhone;

export const MyAlipayJSBridge = (...arr) => {
  if (!IsAlipayBrowser && !IsDingTalkBrowser) {
    use(arr);
    return;
  }
  
  // 钉钉环境
  if (IsDingTalkBrowser) {
    MyDingTalkJs(...arr);
    return;
  }
  
  // 支付宝环境
  if (['back', 'optionMenu'].indexOf(arr[0]) !== -1) {
    document[arr[1].type + 'EventListener'](arr[0], arr[2], arr[1].useCapture || false);
    return;
  }
  
  try {
    if (arr[0] === 'toast') {
      use(arr);
    } else {
      AlipayJSBridge.call(...arr); // eslint-disable-line
    }
  } catch (e) {
    console.log(e);
  }
};

if (!IsAlipayBrowser && !IsDingTalkBrowser) {
  MyAlipayJSBridge('init');
} else if (IsDingTalkBrowser) {
  MyDingTalkJs('init');
  // MyAlipayJSBridge('init');
}

export const MyUrl = url => {
  let config = '';
  if (process.env.NODE_ENV === 'development') {
    config = __config.dev;
  } else {
    config = __config.Default;
  }
  config.PROXY_TABLE_TARGET = 'http://112.124.8.90:8088'
  // 以/api/开头的为向后端api请求
  let reg = /\/api\/.*$/;
  let reg2 = /\/photo\/.*$/;
  if (reg.test(url)) {
    if (config.IS_USE_LOCAL_AJAX) {
      url = `${config.PROXY_TABLE_TARGET + config.GLB_WEBROOT + url}`;
    } else {
      url = `${config.GLB_WEBROOT + url}`;
    }
  } else if (reg2.test(url)) {
    if (config.IS_USE_LOCAL_AJAX) {
      url = `${config.PROXY_TABLE_TARGET + url}`;
    } else {
      url = `${url}`;
    }
  }
  return url;
};

const remSizePx = window.getComputedStyle(document.documentElement)['fontSize'];
export const remSize = Number(remSizePx.substr(0, remSizePx.length - 2));

export const getUrlParam = (name, url) => {
  let curl = url || window.location.search;
  let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
  let r = decodeURI(curl).substr(1).match(reg);
  if (r != null) return unescape(r[2]);
  return null;
};



