let vm;

let exec = (args) => {
  return require.ensure([], () => require('./main'), 'zfb_alipayjs').then(install => {
    vm = install.default;
    vm(args);
    exec = (args) => {
      vm(args);
    };
  });
};

export const use = (args) => {
  if (vm) {
    exec(args);
  } else {
    setTimeout(() => {
      exec(args);
    }, 20);
  }
};



