export const name = {
  path: 'homeList',
  name: 'czy-homeList',
  component: r => require.ensure([], () => r(require('./index.vue')), 'czy_homeList'),
  meta: {title: '财智云'},
  redirect: {name: 'czy-homeList-sr'},
  children: [
    {
      path: 'sr',
      name: 'czy-homeList-sr',
      component: r => require.ensure([], () => r(require('./homeList.vue')), 'czy_homeList'),
      meta: {title: '财智云'}
    }
  ]
};



