/**
 * 将支付宝js使用转换成钉钉js使用，
 * 原则：支付宝实现的钉钉未实现的 不考虑
 *      钉钉实现的支付宝为实现的api 应予保留
 *
 * alert align字段不实现
 * confirm 完全实现
 * toast 支付宝无delay字段，钉钉无成功回掉
 * loading 钉钉比支付宝多showIcon 和 onSuccess两个字段 此外模拟了支付宝的 delay字段实现
 * 返回实现 钉钉的返回android和ios不一致，android返回与支付宝相似(不同之处是移除完所有的事件后支付宝有原始的返回，而钉钉没有)，已经修改ios返回与支付宝返回一致，废除ios的show、control字段
 *    使用时应注意每个事件都要使用e.preventDefault(); （钉钉android需要这个）， 这样能保证三端（支付宝、钉钉、浏览器）一致
 * 设置右侧菜单 已经转换api与支付宝api一致，支持多事件， 使用自定义事件一定要线setOptionMenu以下！ 目前只支持单个菜单
 */
import { IsIOS } from '../utils';
import Vue from 'vue';

window.Vue = Vue;

let loadConfig = {};
let instance;
let optionMenuConfig = {
  onSuccess () {
    instance.$refs.helpToMenu.click();
  }
};

export let MyDingTalkJs = (type, config, func) => {
  loadScript('//g.alicdn.com/dingding/open-develop/1.5.1/dingtalk.js', () => { // eslint-disable-line
    dd.ready(() => {
      exec(type, config, func);
      MyDingTalkJs = exec;
      dd.ui.webViewBounce.disable();// 禁止ios在钉钉中上下弹性滑动
    });
  });
  
  let exec = (type, config, func = () => {}) => {
    // --------------------------------初始化--------------------------------
    if (type === 'init') {
      let Temp = Vue.extend({
        name: 'zfb-dingtalkjs',
        template: `<div style="display: none;">
            <form @submit="helpSubmit">
              <input type="submit" ref="helpToSubmit" value=" "/>,
            </form>
            <button ref="helpToMenu"></button>
          </div>`,
        methods: {
          backEvent () {
            this.$refs.helpToSubmit.click();
          },
          back (config, fun) {
            this.$refs.helpToSubmit[config.type + 'EventListener']('click', fun, config.useCapture || false);
          },
          helpSubmit (e) {
            e.preventDefault();
            _bus._router.back();
          },
          optionMenu (config, fun) {
            this.$refs.helpToMenu[config.type + 'EventListener']('click', fun, config.useCapture || false);
          }
        }
      });
      instance = new Temp({
        el: document.createElement('div')
      });
      document.body.appendChild(instance.$el);
      
      if (IsIOS) {
        dd.biz.navigation.setLeft({
          show: true,
          control: true,
          showIcon: true,
          onSuccess () {
            instance.backEvent();
          }
        });
      } else {
        document.addEventListener('backbutton', (e) => {
          e.preventDefault();
          instance.backEvent();
        }, false);
      }
      
      // 以下 --------------------------------导航相关--------------------------------
    } else if (type == 'back') {
      if (IsIOS) {
        instance.back(config, func);
        if (_.isBoolean(config.showIcon) || !_.isUndefined(config.text)) {
          dd.biz.navigation.setLeft({
            show: true,
            control: true,
            showIcon: _.isBoolean(config.showIcon) ? config.showIcon : true,
            text: config.text || '',
            onSuccess () {
              instance.backEvent();
            }
          });
        }
      } else {
        instance.back(config, func);
      }
    } else if (type === 'setTitle') {
      dd.biz.navigation.setTitle({title: config.title, onSuccess: func});
    } else if (type === 'showOptionMenu') {
      optionMenuConfig.show = true;
      dd.biz.navigation.setRight(optionMenuConfig);
    } else if (type === 'hideOptionMenu') {
      optionMenuConfig.show = false;
      dd.biz.navigation.setRight(optionMenuConfig);
    } else if (type === 'setOptionMenu') {
      optionMenuConfig.text = config.title || '更多';
      let back = optionMenuConfig.control;
      optionMenuConfig.control = !config.reset;
      if ((back !== optionMenuConfig.control) || optionMenuConfig.show) {
        dd.biz.navigation.setRight(optionMenuConfig);
      }
    } else if (type === 'optionMenu') {
      instance.optionMenu(config, func);
      
      // 以下 --------------------------------工具相关--------------------------------
    } else if (['openLink'].indexOf(type) !== -1) {
      dd.biz.util.openLink(Object.assign(config, {'onSuccess': func}));
    } else if (type === 'alert') {
      let configConv = Object.assign({}, {message: config.message, title: config.title, buttonName: config.button || '确定'});
      let resolve = () => {
        func({});
      };
      dd.device.notification.alert(Object.assign(configConv, {'onSuccess': resolve}));
    } else if (type === 'confirm') {
      let configConv = Object.assign({}, {message: config.message, title: config.title, buttonLabels: [config.okButton || '确定', config.cancelButton || '取消']});
      let resolve = (obj) => {
        let msg = {ok: true};
        if (obj.buttonIndex === 1) {
          msg.ok = false;
        }
        func(msg);
      };
      dd.device.notification.confirm(Object.assign(configConv, {'onSuccess': resolve}));
    } else if (type === 'toast') {
      let icon = config.type === 'fail' ? 'error' : config.type;
      let configConv = Object.assign({}, {text: config.content, icon, duration: config.duration ? Math.round(config.duration / 1000) : 2, delay: config.delay ? Math.round(config.delay / 1000) : 0});
      dd.device.notification.toast(configConv);
    } else if (['showLoading', 'hideLoading'].indexOf(type) !== -1) {
      if (type === 'showLoading') {
        Object.assign(loadConfig, {delay: config.delay || 0, isToLoad: true});
        setTimeout(() => {
          if (loadConfig.isToLoad) {
            loadConfig.isToLoad = false;
            dd.device.notification.showPreloader(Object.assign({}, {text: config.text, showIcon: config.showIcon, onSuccess: func}));
          }
        }, loadConfig.delay);
      } else {
        if (loadConfig.isToLoad) {
          loadConfig.isToLoad = false;
        } else {
          dd.device.notification.hidePreloader({onSuccess: func});
        }
      }
    } else if (type === 'exitApp') {
      dd.biz.navigation.close({onSuccess: func});
    } else {
      console.error('暂时未提供该api');
    }
  };
};



