<style type="stylesheet/scss" lang="scss">
	@import "../../scss/variables";
	.page-czy-footer {
		width: 100%;
		position: fixed;
		bottom: 0;
		padding: 0.05rem 0;
		background: #fff;
		height: 0.4rem;
		z-index:105;
		border-top:0.01rem solid #eee;
		.am-flexbox{
			.am-flexbox-item{
				dl{
					color:#888;
					dd{
						font-size: $font-size-smaller;
						margin-bottom: 0.1rem;
					}
					dt{
						line-height: 0.2rem;
					}
				}
				.active_cls{
					dl{
						color:#108ee9;
					}
					dd{
						color:#108ee9;
					}
				}
				.iconfont{
					font-size: $font-size-h3;
				}
			}
		}
	}</style>
<template>
	<div class="page-czy-footer">
		<div class="am-flexbox">
			<div class="am-flexbox-item el-center">
				<router-link :to="{name: 'czy-homeList-sr',query: {close: '1'}}" :class="{'active_cls':srisActive}">
					<dl>
						<dt><i class="icon-shouru iconfont"></i></dt>
						<dd>
							收入
						</dd>
					</dl>
				</router-link>
			</div>
			<div class="am-flexbox-item el-center">
				<router-link :to="{name: 'czy-user-companylist'}" :class="{'active_cls':zcisActive}">
					<dl>
						<dt><i class="icon-wode iconfont"></i></dt>
						<dd>
							我
						</dd>
					</dl>
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'czy-footer',
  data () {
    return {
      srisActive: false,
      zcisActive: false
    }
  },
  created () {
    this.srisActive = true;
  },
  watch: {
    '$route': function (newR, oldR) {
      if (newR.name === 'czy-homeList-sr') {
        this.srisActive = true;
        this.zcisActive = false;
      }
      if (newR.name === 'czy-user-companylist') {
        this.zcisActive = true;
        this.srisActive = false;
      }
    }
  },
  methods: {
  }
}
</script>



