import Vue from 'vue'
import router from './routes'
import VueResource from 'vue-resource';
import FastClick from '../static/libs/fastclick'; // 修复后 fastclick

import _ from 'lodash/core';
import {MyAlipayJSBridge, MyUrl} from './apps/commons/utils';
import components from './components.js';
import { httpInterceptor, utilInterceptor } from './apps/commons/interceptor';
import './scss/index.scss';

Vue.config.productionTip = false;
Vue.use(components);
Vue.use(VueResource);

window.MyAlipayJSBridge = MyAlipayJSBridge;
window.MyUrl = MyUrl;
window.MyOnceHttpConfig = {};
window._ = _;

Vue.$global = Vue.prototype.$global = {};

Vue.http.interceptors.push(httpInterceptor);
Vue.http.interceptors.push(utilInterceptor);

FastClick.attach(document.body);
router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  
  if (to.name === from.name) {
    return;
  }
  
  let meta = to.meta || {};
  let title = '';
  if (meta.title && from.meta && from.meta.title && from.meta.title === meta.title) {
    // console.log('to do noting');
  } else if (meta.title) {
    title = meta.title;
  } else {
    title = to.matched[2].meta.title;
  }
  if (title) {
    MyAlipayJSBridge('setTitle', {
      title
    });
  }
  MyAlipayJSBridge(`${meta.isShowOptionMenu ? 'show' : 'hide'}OptionMenu`);
  next();
});

window._bus = new Vue({
  el: '#app',
  router,
  template: '<div id="app" class="app"><router-view></router-view><czy-footer></czy-footer></div>'
});



