import {HTTP_STATUS} from '../../config';
import Vue from 'vue';

export const httpInterceptor = (request, next) => {
  // noShowError status!==200时 默认弹出错误，（旧的接口
  let oneConfig = Object.assign({noShowError: false}, MyOnceHttpConfig[request.url]);
  let errorMessage = (rejection) => {
    var errorData = {};
    /* status; */
    if (HTTP_STATUS.CODES.NETWORK_ERROR[rejection.status]) {
      errorData.msg = HTTP_STATUS.CODES.NETWORK_ERROR[rejection.status].description;
      errorData.verboseMsg = _.clone(rejection.body, true);
    } else if (HTTP_STATUS.CODES.PROGRAM_ERROR[rejection.status]) { // 500
      errorData.msg = HTTP_STATUS.CODES.PROGRAM_ERROR[rejection.status].description;
      errorData.verboseMsg = _.clone(rejection.body, true);
    } else if (HTTP_STATUS.CODES.COMMUNICATION_ERROR[rejection.status]) {
      // 不处理， 交互错误由后台构造
      if (!rejection.body || ((!rejection.body.msg || !rejection.body.msg.length) && (!rejection.body.verboseMsg || !rejection.body.verboseMsg.length))) {
        errorData.msg = '422错误未返回错误信息';
      }
    } else {
      // 未定义的其他非常用错误
      let showOtherErr = request._showOtherErr ? request._showOtherErr : 'Y';
      if (showOtherErr === 'N') {
        // 当 _isTimeout === N 时，暂时不进行错误提示
        return false;
      } else {
        errorData.msg = request._timeout ? '请求超时' : '网络连接失败，请重试';
        errorData.verboseMsg = JSON.stringify(rejection);
      }
    }
    /**
     * 错误信息
     * @type {{msg: string, verboseMsg: string}}
     */
    rejection.body = _.clone(Object.assign(rejection.body || {}, errorData), true);
    
    // 开发模式，默认开启
    if (!oneConfig.noShowError) {
      let content = request._errorText || (rejection.body ? rejection.body.msg : '服务器繁忙，请稍后再试');
      MyAlipayJSBridge('toast', {
        content,
        type: 'fail',
        duration: 2000
      });
    }
  };
  
  next(response => {
    // 正常状态用户异常状态
    if (HTTP_STATUS.CODES.SUCCESS[response.status]) { // 新的接口 status 200 里有错误返回
      // !_.isUndefined(response.body.retCode) 新的接口
      // _isRetCode 新的接口错误在status ===200 里面， 即body.retCode !== '0' 为错误，默认false 弹出错误
      if (!_.isUndefined(response.body.retCode) && (response.body.retCode != 0) && !request._isRetCode) {
        MyAlipayJSBridge('toast', {
          content: response.body.retMsg || '接口失败',
          type: 'fail',
          duration: 2000
        });
      } else if (request._isBot && !response.body.success) {
        MyAlipayJSBridge('toast', {
          content: response.body.msg,
          type: 'fail',
          duration: 2000
        });
      }
      return response;
    } else if (HTTP_STATUS.CODES.AUTH_ERROR[response.status]) {
      return response;
    } else {
      return errorMessage(response);
    }
  });
};

/**
 * 1、设置超时
 * 2、按钮disabled
 * 3、showLoading
 * 4、状态切换 status
 * 5、加载中与空数据切换 @example {_containerId: 'page-sfxy-xylb', _appendNoData: '暂无记录', _bodyKeys: ['retData', 'sfxyList']}
 */
export const utilInterceptor = (request, next) => {
  let timeout;
  // 如果某个请求设置了_timeout,那么超过该时间，会中断该（abort）请求,并执行请求设置的钩子函数onTimeout方法，不会执行then方法。
  if (request._timeout) {
    timeout = setTimeout(() => {
      request.abort();
      request._cb && request._cb();
    }, request._timeout);
  }
  
  request._target && (request._target.disabled = true);
  
  if (request._showLoading) {
    MyAlipayJSBridge('showLoading', {
      text: request._showLoadingText || '加载中'
    });
  }
  
  if (request._containerId) {
    Vue.nextTick(() => {
      if (document.getElementById(`${request._containerId + '-load'}`)) {
        document.getElementById(`${request._containerId}-load`).style.display = 'block';
      } else {
        let Loading = Vue.extend({
          template:
            `<div class="am-loading page" id=\"${request._containerId + '-load'}\">
            <i class="am-icon loading"></i>
            <div class="am-loading-text">加载中...</div>
          </div>`
        });
        let inst = new Loading({el: document.createElement('div')});
        document.getElementById(request._containerId).appendChild(inst.$el);
      }
    });
  }
  
  next((response) => {
    if (request._lastTime) {
      response.body._lastTime = request._lastTime;
    }
    clearTimeout(timeout);
    request._target && (request._target.disabled = false);
    request._showLoading && MyAlipayJSBridge('hideLoading');
    
    if (request._status) {
      for (let key in request._status) {
        request._status[key] = !request._status[key];
      }
    }
    
    if (request._containerId && response.body.retCode == 0) {
      Vue.nextTick(() => {
        document.getElementById(`${request._containerId}-load`).style.display = 'none';
        let arr = response.body;
        request._bodyKeys.forEach(value => {
          arr = getValue(arr, value);
        });
        
        if (arr.length === 0 && request._appendNoData) {
          if (document.getElementById(`${request._containerId}-noData`)) {
            document.getElementById(`${request._containerId}-noData`).style.display = 'flex';
          } else {
            let elem = document.createElement('div');
            elem.setAttribute('class', 'no-result');
            elem.setAttribute('id', `${request._containerId}-noData`);
            let img = document.createElement('div');
            img.setAttribute('class', 'img');
            let text = document.createElement('p');
            text.innerHTML = request._appendNoData;
            elem.appendChild(img);
            elem.appendChild(text);
            document.getElementById(request._containerId).appendChild(elem);
          }
        } else {
          if (document.getElementById(`${request._containerId}-noData`)) {
            document.getElementById(`${request._containerId}-noData`).style.display = 'none';
          }
        }
      });
    }
    return response;
  });
};

const getValue = (obj, key) => {
  return obj[key];
};



