export const name = {
  path: 'user',
  name: 'czy-user',
  component: r => require.ensure([], () => r(require('./index.vue')), 'czy_user'),
  meta: {title: '财智云'},
  children: [
    {
      path: 'companylist',
      name: 'czy-user-companylist',
      component: r => require.ensure([], () => r(require('./user.vue')), 'czy_user'),
      meta: {title: '财智云'},
    }
  ]
};



