import Vue from 'vue'
import Router from 'vue-router'
import application from './apps/commons/application.vue'

Vue.use(Router);

const modules = [];
modules.push(require('./apps/homeList/route').name);
modules.push(require('./apps/user/route').name);
modules.push(require('./apps/dhcxList/route').name);
modules.push(require('./apps/zdsyList/route').name);
modules.push(require('./apps/dzjk/route').name);
modules.push(require('./apps/public/route').name);
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/czy',
      name: 'czy',
      component: application,
      redirect: {name: 'czy-homeList-sr', query: {close: '1'}},
      children: modules
    },
    {
      path: '*',
      redirect: {name: 'czy-notFound'}
    },
    {
      path: '/',
      redirect: {name: 'czy-homeList-sr', query: {close: '1'}}
    }
  ]
})



