<style type='text/scss' lang="scss">
  @import "../../scss/index.scss";
</style>
<template>
  <div>
    <router-view></router-view>
  </div>
</template>
<script>
import {getUrlParam, IsAlipayBrowser} from './utils';
let backRouteName;
export default {
  name: 'czy',

  created () {
    if (!IsAlipayBrowser) {
      setTimeout(() => {
        this.isHomePage = true;
        let urlStr = `${location.pathname + location.search}`;
        if (location.search) {
          if (!getUrlParam('close') && !getUrlParam('back')) {
            urlStr = `${urlStr}&close=1`;
          } else {
            return;
          }
        } else {
          urlStr = `${urlStr}?close=1`;
        }
        history.replaceState({
          home: 'home'
        }, 'home', urlStr);
        MyAlipayJSBridge('back', {type: 'add'}, this.exitApp);
      }, 50);
    }
  },

  methods: {
    exitApp (e) {
      e.preventDefault();
      if (!_bus.$global.backEvents) {
        MyAlipayJSBridge('exitApp');
      }
    },
    back (e) {
      e.preventDefault();
      this.$router.push({name: backRouteName, query: {close: 1}});
    },
    handleTimeStamp () { // timestamp 区分后退、前进
      let urlStr = `${location.pathname + location.search}`;
      if (location.search) {
        if (!getUrlParam('timestamp')) {
          urlStr = `${urlStr}&timestamp=${new Date().getTime()}`;
        } else {
          urlStr = urlStr.replace(/([?|&]timestamp=)[\w]{1,}/, '');
          setTimeout(() => {
            history.replaceState({
              home: 'home'
            }, 'home', urlStr);
          }, 30);
          return;
        }
      } else {
        urlStr = `${urlStr}?timestamp=${new Date().getTime()}`;
      }
      history.replaceState({
        home: 'home'
      }, 'home', urlStr);
    }
  },

  watch: {
    '$route' (n, o) {
      /**
         * 1、返回到指定页面 query.back
         * 2、关闭webview query.close
         * 3、浏览器环境导航(路由通知)
         */

      if (o.query.back) {
        MyAlipayJSBridge('back', {type: 'remove'}, this.back);
      }
      if (n.query.back) {
        backRouteName = n.query.back;
        MyAlipayJSBridge('back', {type: 'add'}, this.back);
      }

      if (o.query.close || this.isHomePage) {
        MyAlipayJSBridge('back', {type: 'remove'}, this.exitApp);
        this.isHomePage = false;
      }
      if (n.query.close) {
        MyAlipayJSBridge('back', {type: 'add'}, this.exitApp);
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.handleTimeStamp(to, from);
    next();
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (vm.$route.query.back) {
        backRouteName = vm.$route.query.back;
        MyAlipayJSBridge('back', {type: 'add'}, vm.back);
      }
      if (vm.$route.query.close) {
        MyAlipayJSBridge('back', {type: 'add'}, vm.exitApp);
      }
    });
  }
};
</script>



